import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { BootstrapComponent } from './bootstrap/bootstrap.component';
import { WelcomeComponent } from './welcome.component';
import { AdminComponent } from '././checkin/admin/checkin-admin.component';
import { CheckoutQRScanComponent } from '././checkin/checkout-guest.component';
import { CheckoutMessageComponent } from '././checkin/checkout-message.component';

const routes: Routes = [
    { path: 'bootstrap', component: BootstrapComponent },
    { path: 'welcome', component: WelcomeComponent },
    {
        path: 'explore',
        loadChildren: () =>
            import('./explore/explore.module').then((m) => m.AppExploreModule),
    },
    {
        path: 'checkin',
        loadChildren: () =>
            import('./checkin/checkin.module').then(
                (m) => m.VisitorCheckinModule
            ),
    },
    {
        path: 'admin',
        loadChildren: () =>
            import('./checkin/admin/admin.module').then(
                (m) => m.AdminCheckinModule
            ),
    },
    { path: 'checkout', component: CheckoutQRScanComponent },
    { path: 'checkoutmessage', component: CheckoutMessageComponent },
    { path: '**', redirectTo: 'bootstrap' },
];

@NgModule({
    imports: [RouterModule.forRoot(routes, { useHash: true })],
    exports: [RouterModule],
})
export class AppRoutingModule {}
