import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateService } from '@ngx-translate/core';
import { first } from 'rxjs/operators';
import { CheckinStateService } from './checkin-state.service';

@Component({
    selector: 'checkin-results',
    template: `
        <div
            class="bg-white rounded shadow overflow-hidden relative flex flex-col items-center my-4 p-4 mx-auto"
        >
            <h3 class="text-xl">{{ 'You are checkedout' | translate }}</h3>
            <p class="text-center my-4">
                {{
                    'You have been checkedout, Thank you for coming!'
                        | translate
                }}
            </p>
            <a button mat-button [routerLink]="['/checkout']">
                {{ 'Done' | translate }}
            </a>
        </div>
    `,
    styles: [
        `
            :host {
                position: absolute;
                display: flex;
                align-items: center;
                justify-content: center;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
                background-color: rgba(0, 0, 0, 0.5);
            }

            :host > div {
                width: 32rem;
                max-width: calc(100vw - 2rem);
            }

            [button] {
                min-width: 8rem;
            }
        `,
    ],
})
export class CheckoutMessageComponent {
    public readonly event = this._checkin.event;
    public readonly guest = this._checkin.guest;

    constructor(
        private _checkin: CheckinStateService,
        private _router: Router,
        private translateService: TranslateService
    ) {
        this.translateService.setDefaultLang('en');
        this.translateService.use(localStorage.getItem('lang') || 'en');
    }

    public ngOnInit(): void {
        this.event.pipe(first()).subscribe((_) => (!_ ? this.previous() : ''));
    }

    public previous(): void {
        this._router.navigate(['/checkout']);
    }
}
