import { Injectable } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { HashMap, notifyError } from '@user-interfaces/common';
import { CalendarEvent, EventsService } from '@user-interfaces/events';
import {
    GuestsService,
    GuestUser,
    generateGuestForm,
} from '@user-interfaces/users';
import { isSameDay } from 'date-fns';
import { BehaviorSubject } from 'rxjs';

import { HttpClient } from '@angular/common/http';

@Injectable({
    providedIn: 'root',
})
export class CheckinStateService {
    /** Current event being checked in */
    private _event = new BehaviorSubject<CalendarEvent>(null);
    /** Current guest being checked in */
    private _guest = new BehaviorSubject<GuestUser>(null);
    /** Photo of the current guest */
    private _photo = new BehaviorSubject<string>('');
    /** Photo of the current guest */
    private _error = new BehaviorSubject<string>('');
    /** Form for the current guest details */
    private _form = new BehaviorSubject<FormGroup>(null);

    public entry = true;

    public eventend: Number;
    public eventstart: Number;
    public email: any;
    public eventid: any;

    public readonly event = this._event.asObservable();
    public readonly guest = this._guest.asObservable();
    public readonly error = this._error.asObservable();
    public readonly form = this._form.asObservable();

    constructor(
        private _guests: GuestsService,
        private _events: EventsService,

        public http: HttpClient
    ) {}

    public clear() {
        this._guest.next(null);
        this._event.next(null);
        this._photo.next(null);
    }

    public setPhoto(data: string) {
        this._photo.next(data);
    }

    public setError(message: string) {
        this._error.next(message);
    }

    /** Load gie */
    public async loadGuestAndEvent(email: string, event_id?: string) {
        const guest = await this._guests.show(email);
        console.log(guest);
        if (guest === null) {
            throw new Error('Guest Not found');
        }
        if (event_id) {
            const event = await this._events.show(event_id);
            this._guest.next(guest);
            this._event.next(event);

            this._form.next(
                generateGuestForm(guest, event.host, event.hostphone)
            );
            return { guest, event };
        }
        const upcoming = await this._guests.meetings(email);
        const today = new Date();
        const todays_events = upcoming.filter((event) =>
            isSameDay(new Date(event.date), today)
        );
        todays_events.sort((a, b) => a.date - b.date);
        if (todays_events.length <= 0) {
            throw new Error(`No meetings for with guest "${email}" today`);
        }
        this._guest.next(guest);
        this._event.next(todays_events[0]);
        this._form.next(generateGuestForm(guest, todays_events[0].host));
        return { guest, event: todays_events[0] };
    }

    public async updateGuest(data?: HashMap) {
        const guest = this._guest.getValue();
        const form = this._form.getValue();
        if (!guest || !form) return;
        await this._guests.updateMetadata(guest.email, {
            ...guest,
            ...form.value,
            ...(data || {}),
        });
    }

    public async checkinGuest() {
        const guest = this._guest.getValue();
        const event = this._event.getValue();
        var current = new Date();
        var checkint = parseInt((Date.now() / 100).toString());
        const host = event.host;
        this.eventid = event.id;
        this.eventstart = event.event_start;
        this.eventend = event.event_end;
        const name = guest.name;
        const email = guest.email;
        this.email = email;
        const phone = guest.phone;
        const organization = guest.organization;
        const date = current.toLocaleDateString();
        const time = current.toLocaleTimeString();

        // let Record = {};
        // Record['host'] = event.host;
        // Record['name'] = guest.name;
        // Record['email'] = guest.email;
        // Record['phone'] = guest.phone;
        // Record['organisation'] = guest.organisation;
        // Record['date'] = current.toLocaleDateString();
        // Record['checkin time'] = current.toLocaleTimeString();

        var body = {};
        body['host'] = host;
        body['name'] = name;
        body['email'] = email;
        body['phone'] = phone;
        body['organization'] = organization;
        body['date'] = date;
        body['time'] = time;
        body['checkin'] = checkint;
        body['eventid'] = this.eventid;

        //     this.http.get<any>('http://localhost:1234/visitorcheck').subscribe(data => {
        //     console.log(data)
        // },)

        // console.log(body);
        // this.http.get<any>('http://localhost:1234/').subscribe((data) => {
        //     console.log(data);
        // });

        await this.http
            .post<any>('http://localhost:3000/visitorcheck', body)
            .toPromise()
            .then((data) => {
                console.log('yyrryyryyr' + data);

                if (data === null) {
                    console.log('sdfsdfsdfsdfsdfsdfsdf');
                    this.entry = false;
                }
            });

        //firbase database

        // this.create_Newemployee(Record).then(res=>{

        // });

        if (!guest || !event) return;
        this._events.checkInGuest(event.id, guest.email, {
            system_id: event.system?.id || event.resources[0]?.id,
            state: true,
        });
    }

    //firebaee database
    // create_Newemployee(Record){
    //     return this.firesevices.collection("Visitors").add(Record)
    // }

    public printPass() {
        try {
            // TODO: actually trigger print visitor pass
            return new Promise((res) => setTimeout(() => res(), 5000));
        } catch (err) {
            notifyError('Error printing visitor pass');
        }
        return Promise.reject();
    }

    public userInfo() {
        const guest = this._guest.getValue();
        const name = guest.name;
        return name;
    }
}
